/* App.css */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  scroll-behavior: smooth;
  background-color: #f4f4f4;
  color: #333;
}

.app {
  text-align: center;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #282c34;
  padding: 10px 20px;
  z-index: 1000;
  color: white;
}

.header nav {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.header nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
  padding: 10px;
}

.header nav a:hover {
  background-color: #444;
  border-radius: 5px;
}

.section {
  padding: 100px 20px;
  margin-top: 60px;
}

.section h2 {
  margin-top: 0;
}

.section ul {
  list-style: none;
  padding: 0;
}

.section ul li {
  margin-bottom: 10px;
}

.footer {
  padding: 20px;
  background-color: #282c34;
  color: white;
}

.footer .social-links {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}

.footer .social-links a {
  color: white;
  text-decoration: none;
}

.footer .social-links a:hover {
  text-decoration: underline;
}

.home-section {
  background: url('https://via.placeholder.com/1920x1080') no-repeat center center/cover;
  color: white;
}

.about-section, .education-section, .experience-section, .projects-section, .skills-section, .contact-section {
  background-color: #fff;
}

.contact-section .social-links a {
  margin: 0 10px;
  padding: 10px 20px;
  border: 1px solid #333;
  border-radius: 5px;
  text-decoration: none;
  color: #333;
}

.contact-section .social-links a:hover {
  background-color: #333;
  color: white;
}
